import React from 'react'
import { Link } from 'react-router-dom';

function Breadcrumbs(params) {

    return (
        <nav class="breadcrumb" aria-label="breadcrumbs">
            <ul>
                {params.links && params.links.map((link, i) => (
                    <li className={i === params.links.length - 1 ? 'is-active' : ''}>
                        <Link to={link.href} className='breadcrumb-link'>{link.name}</Link>
                    </li>
                ))}
            </ul>
        </nav>
    );
}

export default Breadcrumbs