import React, { useState } from 'react'
import { Link } from 'react-router-dom'

import Tabs from './global/Tabs'
import PrimaryButton from './global/buttons/PrimaryButton'
import ShareButton from './global/buttons/ShareButton'
import ShareButtonV2 from './global/buttons/ShareButtonV2'
import EditButton from './global/buttons/EditButton'
import EditButtonV2 from './global/buttons/EditButtonV2'
import ArchiveButton from './global/buttons/ArchiveButton'
import RepostButton from './global/buttons/RepostButton'
import Controls from './global/Controls'
import Breadcrumbs from './global/Breadcrumbs'
import InlineIconLabel from './global/InlineIconLabel'
import {
  RouteJobCandidatesNewMatches,
  RouteJobCandidatesInProcess,
  RouteJobCandidatesCandidatesAccepted
} from './global/routes'
import { Context } from "./App"
import { Purple } from './global/colors'

function JobsOverview() {
  const { state, dispatch } = React.useContext(Context)

  const [index, setIndex] = useState(0)
  return (
    <div>

      <Breadcrumbs links={[
        {
          name: 'Jobs Overview',
        },
      ]} />

      <div className='columns'>
        <div className='column is-6'>
          <Tabs
            tabs={['Active Jobs', 'Archived Jobs']}
            position={index}
            setPosition={setIndex}
          />
        </div>
        <div className='column is-6' style={{ textAlign: 'right' }}>
          <Link to='/jobs/new'>
            <PrimaryButton
              label='Post New Job'
            />
          </Link>

        </div>
      </div>
      {index === 0 && state.recruiter.activeJobs && Object.keys(state.recruiter.activeJobs).map(id => (
        <JobOverview
          position={state.recruiter.activeJobs[id].position}
          location={state.recruiter.activeJobs[id].location}
          new_messages={state.recruiter.activeJobs[id].new_messages}
          dispatch={dispatch}
          controls={[
            <Link to='/edit' className='icon-link'><EditButton /></Link>,
            <ShareButton />
          ]}
        >
          <div style={{ paddingBottom: 8 }}>
            <Link to={RouteJobCandidatesNewMatches(id)}>
              <InlineIconLabel icon='fas fa-rocket'>
                <strong>{Object.keys(state.recruiter.activeJobs[id].new_matches).length}</strong> New Matches
                </InlineIconLabel>
            </Link>
          </div>
          <div style={{ paddingBottom: 8 }}>
            <Link to={RouteJobCandidatesInProcess(id)}>
              <InlineIconLabel icon='fas fa-comment-dots'>
                <strong>{Object.keys(state.recruiter.activeJobs[id].in_process).length}</strong> In Process
                </InlineIconLabel>
            </Link>
          </div>
          <div>
            <Link to={RouteJobCandidatesCandidatesAccepted(id)}>
              <InlineIconLabel icon='fas fa-user-check'>
                <strong>{Object.keys(state.recruiter.activeJobs[id].accepted_candidates).length}</strong> Candidates Accepted
                </InlineIconLabel>
            </Link>
          </div>
        </JobOverview>
      ))}

      {index === 0 && state.recruiter.activeJobs.length === 0 && <div style={{ marginTop: 24, textAlign: 'center' }}>
        <h3>No jobs, yet</h3>
        <p>You have yet to create a job posting. But nothing is stopping you. Just smash that `Post New Job` button and I think you'll find, Bob's your uncle.</p>
      </div>}

      {index === 1 && state.recruiter.archivedJobs && Object.keys(state.recruiter.archivedJobs).map(id => (
        <JobOverview
          position={state.recruiter.archivedJobs[id].position}
          location={state.recruiter.archivedJobs[id].location}
          controls={[
            <RepostButton />
          ]}
        >
          <div className='columns is-gapless is-marginless' style={{ paddingTop: 24, paddingBottom: 12 }}>
            <div className='column is-2'>
              <InlineIconLabel icon='fas fa-rocket' color='#D1D1D6'>
                <b>{state.recruiter.archivedJobs[id].conversations}</b> Conversations
              </InlineIconLabel>
            </div>
            <div className='column is-3'>
              <InlineIconLabel icon='fas fa-comment-dots' color='#D1D1D6'>
                <b>{state.recruiter.archivedJobs[id].candidates_forwarded}</b> Candidates Forwarded
              </InlineIconLabel>
            </div>
            <div className='column is-7' style={{ textAlign: 'right', paddingTop: 2, fontSize: 14, color: '#9B9B9B' }}>
              <strong style={{ color: '#9B9B9B' }}>Expiration date:</strong> {state.recruiter.archivedJobs[id].expiration_date}
            </div>
          </div>
        </JobOverview>
      ))}

      {index === 1 && state.recruiter.archivedJobs.length === 0 && <div style={{ marginTop: 24, textAlign: 'center' }}>
        <h3>Too soon</h3>
        <p>None of your jobs have expired, visit here in the future to see you expired jobs.</p>
      </div>}

    </div >
  )
}

export default JobsOverview

function JobOverview(params) {
  return (
    <div style={{ borderBottom: '1px solid #E4E4E4', marginBottom: 24 }}>
      <div className='columns is-gapless is-marginless'>
        <div className='column is-6'>
          <span>
            <h2 style={{ display: 'inline', marginRight: 12 }}>{params.position}</h2>
          </span>
          <div style={{ marginTop: 42, paddingBottom: 12 }}>
            <span style={{ marginRight: 48 }}>
              <Link to={'/edit'}>
                <EditButtonV2 />
              </Link>
            </span>
            <span style={{ marginRight: 48 }}>
              <ShareButtonV2 />
            </span>
            <span style={{ marginRight: 48 }}>
              <ArchiveButton />
            </span>
          </div>
        </div>
        <div className='column is-4' style={{ marginTop: 12 }}>
          {(params.new_messages || params.new_messages === 0) && <span
            className="new-messages-badge"
            style={{
              backgroundColor: `${params.new_messages > 0 ? '#956EDC' : '#E4E4E4'}`,
              color: '#FFFFFF',
              padding: 4,
              paddingLeft: 8,
              paddingRight: 8,
              fontSize: 14,
            }}
            onClick={() => {
              params.dispatch({ type: 'SHOW_MESSAGES_WARNING' })
              const timer = setTimeout(() => {
                params.dispatch({ type: 'HIDE_MESSAGES_WARNING' })
              }, 10000)
              return () => clearTimeout(timer)
            }}
          >
            <strong style={{ color: '#FFFFFF' }}>{params.new_messages}</strong> New Message{params.new_messages > 1 ? 's' : ''}
          </span>}

        </div>
        <div className='column is-2'>
          <div className=' job-overview-side-candidates'>
            {params.children}
          </div>
        </div>
      </div>
    </div>
  )
}