function App() {
  console.log('v7')
  // fetch('https://0jzvzdhkz4.execute-api.eu-west-1.amazonaws.com/prod/testing')
  //   .then(res => res.json())
  //   .then(
  //     (result) => {
  //       console.log(result)
  //     })

  const [state, dispatch] = React.useReducer(reducer, initialState);
  return (
    <Context.Provider value={{
      state,
      dispatch
    }}>
      <Router basename={process.env.REACT_APP_ROUTER_BASENAME}>
        <NavBar />

        {state.notifications.show_messages_warning && <div
          class='notification is-primary'
          onClick={() => dispatch({ type: 'HIDE_MESSAGES_WARNING' })}
          style={{ position: 'absolute', zIndex: 30, top: 70, left: 20 }}>
          <button class='delete'></button>
          In the future, this will link to the messaging part of Sboj. Stay tuned.
      </div>}

        {state.notifications.show_welcome && <Modal
          closeOnClick={() => dispatch({ type: 'HIDE_WELCOME_MODAL' })}
          textAlign='left'
          buttonAlign='right'
        >
          <h3>Thanks for being part of the Sboj.io test team</h3>
          <p>This update we focused primarily on the recruiting process. We worked hard to make sure that flow and information is clear and intuitive.</p>
          <br />
          <p>As this is very early stages please be advised:</p>
          <br />
          <ul>
            <li>All data is random output (names, skill sets, previous job exp etc.) so there is a chance that the profiles might not make sense. </li>
            <li>Repetition of profile pictures</li>
            <li>Anything linked to the messaging side of the platform is due to be released in the next update! </li>
          </ul>
          <br />
          <p>We look forward to your honest feedback. We are looking to answer questions such as</p>
          <br />
          <ul>
            <li>Is this easier to use than other platforms?</li>
            <li>Would you post a job on this site?</li>
            <li>If you could what would you add / remove?</li>
            <li>Whats the most important aspect of a job board for you?</li>
          </ul>
          <br />
          <p>Thank you again</p>
          <p>Sboj team</p>
        </Modal>}

        <section className='section'>
          <div className='container'>
            <Route exact path='/' component={Job} />
            <Route exact path='/jobs/new' component={JobForm} />
            <Route exact path='/edit' component={EditJobForm} />
            <Route exact path='/candidate/:id' component={Profile} />
            <Route exact path='/job/:id' component={Job} />
            <Route exact path='/jobs-overview' component={JobsOverview} />
            <Route exact path='/job/:id/candidates' component={JobCandidates} />
            <Route exact path='/job/:id/candidates/:page' component={JobCandidates} />
            <Route exact path='/job/:id/candidates/:page/:profile_id' component={Profile} />
          </div>
        </section>
        <footer className='footer'>
          <div className='content has-text-centered'>
            <p>
              {randomFooterMessages}
            </p>
          </div>
        </footer>
      </Router>
    </Context.Provider>
  );
}

export default App
